// global
@import url('https://fonts.googleapis.com/css?family=Montserrat|Open+Sans:400,700&display=swap');

$brown: #F8DFBD;
$black: #000000;
$white: #FFFFFF;

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Open Sans', sans-serif;
    margin: 0;
}

p,
a,
body {
    font-family: 'montserrat', sans-serif;
}

body {
    background-color: $brown;
}

.main-wrapper {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}

// header styles
.header {
    padding: 20px 0;
    margin-top: 60px;

    .logo {
        display: flex;
        justify-content: center;

        img {
            width: 300px;
        }
    }
}

// back btn
.back-btn {
    text-align: center;
    margin: 60px 0;

    a {
        text-decoration: none;
        border: 2px solid $black;
        padding: 12px 40px;
        color: $white;
        background-color: $black;
    }
}

// time styles
.timer {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    text-align: center;
    background-color: red;
    color: $white;
}

// home games section styles
.games {
    margin-top: 15px;
    text-align: center;

    h2 {
        margin-bottom: 15px;
    }

    .games-list {
        padding: 0;
        margin: 0;

        .nav-item {
            color: $black;
            text-decoration: none;

            .game {
                padding: 10px 0;
            }
        }
    }
}

// games rules page
.game-rules {
    margin-top: 40px;
    text-align: center;

    .rules {
        text-align: left;
        margin: 0;
        padding: 20px 0px 0px 18px;

        .rule {
            padding: 8px 0;
        }
    }
}

.other-rules {
    text-align: center;
    margin-top: 30px;

    a {
        font-family: 'Open Sans';
        font-weight: 600;
        text-decoration: none;
        color: $black;
    }
}